import { Create_Login_User, Update_User, Logout } from "./types";
import axios from "axios";
import history from "../history";
import { checkStatus } from "../lib";

export const Create_Login_User_Action = ({ userName, password }) => {
  return async (dispatch, getState) => {
    console.log("inputs", userName, password);
    await axios
      .post(`${document.location.origin}/authenticate`, {
        userName,
        password,
      })
      .then((res) => {
        console.log("res from server", res);
        dispatch({
          type: Create_Login_User,
          payload: {
            ...res.data,
          },
        });
        history.push(checkStatus(res.data));
      })
      .catch((err) => {
        console.log(err);
      });
  };
};

export const Update_User_Action = (inputs) => {
  return async (dispatch, getState) => {
    const previousState = getState();
    console.log(`${document.location.origin}/user`);
    await axios
      .put(`${document.location.origin}/user`, {
        ...previousState,
        ...inputs,
      })
      .then((res) => {
        console.log("retrieved data-actionCreator", res.data);
        dispatch({
          type: Update_User,
          payload: {
            ...res.data,
          },
        });
        history.push(checkStatus(res.data));
      })
      .catch((err) => {
        console.log(err);
      });
  };
};
export const Logout_Action = () => {
  return (dispatch) => {
    dispatch({
      type: Logout,
    });
    history.push("/");
  };
};
